export default {
	data() {
		return {};
	},
	computed: {
		i18n() {
			return this.$t('index');
		}
	},
	onShow() {
		uni.setNavigationBarTitle({
				title: this.$t('index.set')
		})		
	},
	methods: {
		xieyi(){
			uni.navigateTo({ 
				url: "../yonghuxieyi/yonghuxieyi"
			});
		},
		zhengce(){
			uni.navigateTo({ 
				url: "../yinsizhengce/yinsizhengce"
			});
		},
		aboutUs(){
			uni.navigateTo({ 
				url: "../aboutUS/aboutUS"
			});
		},
		password(){
			uni.navigateTo({
				url: "../setting/setting"
			});
		},
		changeUs(){
			uni.showModal({
			    title: '提示',
			    content: '确认退出当前账号',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
						uni.removeStorageSync('storage_token');
						uni.showToast({
							title: '操作成功',
							icon: 'none',
							duration: 800
						});
						uni.navigateTo({
							url: '../login/login'
						});
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			
		}
	}
};
